.product-page {
	&__block {
		margin: 0;
		height: 32px;
		margin-bottom: 80px;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	&__buttons {
		box-sizing: border-box;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		overflow: hidden;

		&-list {
			display: flex;
			justify-content: space-between
		}
	}
}

.pagination-right {
	margin-left: 8px;
}